html {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  min-height: 100%;
}

body {
  margin: 0 auto;
  background: #fff;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.wrap {
  height: 100%;
  width: 100%;
  background-image: url(assets/images/home-page-wheat-high50.jpg);
  background-attachment: fixed;
  /*background-position: top center;*/
  background-size: cover;
  background-repeat: no-repeat;
}

video[autoplay]:not([muted]) {
  display: none;
}

img {
  display: block;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 300;
  height: auto;
  line-height: 2;
  position: relative;
  text-align: center;
}

img:before {
  content: "We're sorry, the image below is broken :(";
  display: block;
  margin-bottom: 10px;
}

img:after {
  content: "(url: " attr(src) ")";
  display: block;
  font-size: 12px;
}

hr {
  margin: 1em 0;
}

.screen-reader-response {
  position: absolute;
  left: -9999%;
  visibility: hidden;
}

@media screen and (max-width: 782px) {
  :root {
    margin-top: 0 !important;
  }

  #wpadminbar {
    display: none;
  }
}

.vc_inline-link {
  display: none !important;
}
